import * as constants from './constants';
import {message} from "antd";

//
import {
    requestGetHuaYanShiDataByTableNameAndDate,
    requestSaveHuaYanShiData,
} from "../../../../http/request/RequestHuaYanShi"
import {
    Mark,
    Table
} from "../../../../http/constant/Constant"
import {
    HuaYanShiFormat,
    updateOperator
} from "../../../../Helper/Format"


import {deepCopy} from "../../../../Helper/Copy";


/**
 * 
 * @param timeChose
 * @returns {{timeChose: *, type: string}}
 */
export const changeTimeChose = (timeChose) => ({
    type: constants.CHANGE_TIME_CHOSE_RMA_FMHG_SHS,
    timeChose: timeChose
});

//
export function doChangeTimeChose(timeChose) {

    return (dispatch) => {
        dispatch(changeTimeChose(timeChose));
    }

}


export const updateData = ({data}) => ({
    type: constants.UPDATE_DATA_RMA_FMHG_SHS,
    data: data
});

//
export const updateStandard = (startValue, endValue) => ({
    type: constants.UPDATE_STANDARD_RMA_FMHG_SHS,
    startValue: startValue,
    endValue: endValue
});


/**
 *
 * @param date
 * @param tableName
 * @param data 
 * @returns {Function}
 */
export const getData = (date, tableName, data) => {
    return (dispatch) => {

        requestGetHuaYanShiDataByTableNameAndDate(
            date,
            tableName,
            data
        ).then((response) => {

            if(response['code'] === 0){
                //
                //
                let newData = deepCopy(response['data']);
                let result = HuaYanShiFormat(
                    data,
                    newData,
                    tableName
                );

                dispatch(updateData({//
                    data: result[0]
                }));

                //
                dispatch(updateStandard(result[1], result[2]));
            }



        });//end requestGetHuaYanShiDataByTableNameAndDate
    }
};//end getData


export function saveData(
    {
        tableType = 1,//
        date,
        index,
        tableName,
        data,
        num = 1//1
    }
) {

    return(dispatch) =>{


        requestSaveHuaYanShiData({
            date: date,
            index: index,
            duty: window.localStorage.duty,
            tableName: tableName,
            authority: window.localStorage.authority,
            data: data,
            num: num
        }).then((response) => {

            if(response['code'] === Mark.SUCCESS){
                message.info('');
            }else {
                message.info('');
            }



            //
            if (tableType === Table.UPPER_TABLE) {//
                if (num === 1)//
                    updateOperator({
                        data: data,
                        index: index,
                        num:num
                    });
                else//
                    updateOperator({
                        data: data,
                        num: 30
                    });//30

                dispatch(updateData({data:data}))//updateData

            }else {
                dispatch(updateData({data:data}))
            }

        });//end requestSaveHuaYanShiData
    }

}
