import * as constants from './constants';
import {fromJS} from 'immutable';
import moment from 'moment';
import {TableName} from "../../../../Constant/TableNameConstant"

const defaultState = fromJS({

    // 2020/2/27 YYYY/MM/DD 
    date: moment().format("YYYY/MM/DD"),//moment().format("YYYY-MM-DD"),
    requestFlag:true,//
    timeChose: 0,//0
    data:[//
        {data: []}, {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []}, {data: []},//0-7 0-7
        {data: []}, {data: []}, {data: []}, {data: ['', '', moment().format("YYYY/MM/DD hh:mm:ss").toString()]},// 8-11

        {data: []}, {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []}, {data: []},//8-15 12-19
        {data: []}, {data: []}, {data: []}, {data: [
                '', '', moment().format("YYYY/MM/DD hh:mm:ss").toString()
            ]},// 20-23

        {data: []}, {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []}, {data: []},//16-23 24-31
        {data: []}, {data: []}, {data: []}, {data: [
                '', '', moment().format("YYYY/MM/DD hh:mm:ss").toString()
            ]},// 32-35

    ],
    modelData:[//
        {data: []}, {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []}, {data: []},//0-7 0-7
        {data: []}, {data: []}, {data: []}, {data: ['', '', moment().format("YYYY/MM/DD hh:mm:ss").toString()]},// 8-11

        {data: []}, {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []}, {data: []},//8-15 12-19
        {data: []}, {data: []}, {data: []}, {data: [
                '', '', moment().format("YYYY/MM/DD hh:mm:ss").toString()
            ]},// 20-23

        {data: []}, {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []}, {data: []},//16-23 24-31
        {data: []}, {data: []}, {data: []}, {data: [
                '', '', moment().format("YYYY/MM/DD hh:mm:ss").toString()
            ]},// 32-35

    ],
    allTime:[
        ['0:00', '1:00', '2:00', '3:00', '4:00', '5:00', '6:00', '7:00'],
        ['8:00', '9:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00'],
        ['16:00', '17:00', '18:00', '19:00', '20:00', '21:00', '22:00', '23:00']
    ],
    person: window.localStorage.username, //
    tableName:TableName.ZHONG_KONG_SHI_SCXT_YXJL//

});

export default (state = defaultState, action) => {

    switch (action.type) {

        case constants.CHANGE_TIME_CHOSE_BSO:
            return state.set('timeChose', action.timeChose);
        case constants.UPDATE_DATA_BSO:
            // console.log("requestFlag false")
            return state.merge({
                'data': action.data,
                'requestFlag':false//true
            });
        default:
            return state;
    }


}