import * as constants from './constants';
import {fromJS} from 'immutable';
import moment from 'moment';
import {TableName} from "../../../../Constant/TableNameConstant"

const defaultState = fromJS({

    // 2020/2/27 YYYY/MM/DD 
    date: moment().format("YYYY/MM/DD"),//moment().format("YYYY-MM-DD"),
    requestFlag:true,//
    timeChose: 0,//0
    data:[//
        {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []},
        {data: []},{data: []},

        {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []},
        {data: []}, {data: []},{data: []},
        {data: []},{data: []},
        
        {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []},
        {data: []},{data: []},
    ],
     modelData : [//
        {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []},
        {data: []},{data: []},

        {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []},
        {data: []}, {data: []},{data: []},
        {data: []},{data: []},

        {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []},
        {data: []},{data: []},
    ],
    LX: ['','','','','','','','',''] ,//,
    allTime :[
        ['0','','','0','','','0','',''],
        ['8','','','8','','','8','',''],
        ['4','','','4','','','4','','']
    ],
    person: window.localStorage.username, //
    tableName:TableName.ZHONG_KONG_SHI_CDR//

});
export default (state = defaultState, action) => {

    switch (action.type) {
        case constants.UPDATE_DATA_CCD:
            return state.merge({
                'data': action.data,
                'requestFlag':false//true
            });
        case constants.CHANGE_TIME_CHOSE_CCD:
            return state.set('timeChose', action.timeChose);

        default:
            return state;
    }


}