export default (state = defaultState, action) => {

  switch (action.type) {
    case constants.UPDATE_DATA_CMS://
      return state.merge({
        data: action.data,
        requestFlag: false
      });
    case constants.CHANGE_TIME_CHOSE_CMS://
      return state.set('timeChose', action.timeChose);

    case constants.UPDATE_STANDARD_CMS://
      return state.merge({
        'startValue': action.startValue,
        'endValue': action.endValue
      });
    case constants.UPDATE_UPPER_DATA_FRONT_CMS:// SiO2~MgO
      return state.set('upperDataFront', action.upperDataFront);

    case constants.UPDATE_UPPER_DATA_LAST_CMS:
      return state.merge({
        'upperDataLast': action.upperDataLast,
        'requestFlag': false
      });


    default:
      return state;
  }


}