export function requestSaveZhongKongShiData(
    {
        date,
        index,
        departmentId,
        tableName,
        id,
        data,
        num,
    }) {
    return new Promise((resolve, reject) => {



        RequestCenter({
            url: URL.REQUEST_SAVE_ZHONGKONG_DATA,
            jsonData: getSaveZhongKongShiDataJson(//json
                {
                    date: date,
                    index: index,
                    departmentId: departmentId,
                    tableName: tableName,
                    userId: id,
                    data: data,
                    num: num
                }
            ),
            flag: RequestMethod.jsonDta,
        })
            .then((response) => {
                // 
                //TODO  requestSaveHuaYanShiData


                resolve(response['code'])//code
            })
            .catch(
            )
    });

}

export function requestSaveFeedBackData(
        username,
        classification,
        title,
        content,
    ) {
    return new Promise((resolve, reject) => {
        const formData = new FormData();
        formData.append('username', username);
        formData.append('classification', classification);
        formData.append('title', title);
        formData.append('content', content);
        RequestCenter({
            url: URL.REQUEST_SAVE_FEEDBACK_DATA,
            formData:formData,
        })
            .then((response) => {
                // 
                //TODO  requestSaveHuaYanShiData


                resolve(response['code'])//code
            })
            .catch(
            )
    });

}