import * as constants from './constants';
import {message} from "antd";

//
import {
    requestGetHuaYanShiDataByTableNameAndDate,
    requestSaveHuaYanShiData,
    getHuaYanShiDataDifferenceValueBytableNameAndDate,
} from "../../../../http/request/RequestHuaYanShi"
import {
    Mark,
    Table,
    Department
} from "../../../../http/constant/Constant"
import {
    HuaYanShiFormat,
    updateOperator
} from "../../../../Helper/Format"


import moment from "./reducer";
import {deepCopy} from "../../../../Helper/Copy";

/**
 * 
 * @param timeChose
 * @returns {{timeChose: *, type: string}}
 */
/*export const changeTimeChose = (timeChose) => ({
    type: constants.CHANGE_TIME_CHOSE_SMIAO,
    timeChose: timeChose
})*/


export const updateData = ({data}) => ({
    type: constants.UPDATE_DATA_TFKT,
    data: data
});


/**
 *
 * @param date
 * @param tableName
 * @param data 
 * @returns {Function}
 */
export const getData = (date, tableName, data) => {
    return (dispatch) => {

        requestGetHuaYanShiDataByTableNameAndDate(
            date,
            tableName,
            data
        ).then((response) => {

            if(response['code'] === 0){
                //
                //
                let newData = deepCopy(response['data']);
                let result = HuaYanShiFormat(
                    data,
                    newData,
                    tableName
                );

                dispatch(updateData({//
                    data: result[0]
                }));

                //
                //dispatch(updateStandard(result[1], result[2]));
            }

        });//end requestGetHuaYanShiDataByTableNameAndDate
    }
};//end getData


export function saveData(
    {
        tableType = 1,//
        date,
        index = 0,
        tableName,
        data,
        num = 1//1
    }
) {

    return(dispatch) =>{
        requestSaveHuaYanShiData({
            date: date,
            index: index,
            //department: Department.DEPARTMENT_HUAYS,
            duty: window.localStorage.duty,
            tableName: tableName,
            authority: window.localStorage.authority,
            data: data,
            num: num
        }).then((response) => {

            //
            if (response['code'] == Mark.SUCCESS && num == 1) {
                message.info('');
            } else if (response['code'] == Mark.SUCCESS && num > 1) {
                message.info('');
            } else {
                message.info('');
            }

            //
            if (tableType === Table.UPPER_TABLE) {//
                if (num === 1)//
                    updateOperator({
                        data: data,
                        index: index,
                        num:num
                    })
                else//
                    updateOperator({
                        data: data,
                        num: 1
                    })//24

                dispatch(updateData({data:data}))//updateData

            }else {
                dispatch(updateData({data:data}))
            }

        });//end requestSaveHuaYanShiData
    }

};


export const queryData = (date, tableName, data) => {
    return (dispatch) => {

        getHuaYanShiDataDifferenceValueBytableNameAndDate(
            date,
            tableName,
            data
        ).then((response) => {

            if(response['code'] === 0){
                //
                //
                let newData = deepCopy(response['data']);
            
                if(newData == null){
                    message.info('');
                    dispatch(updateData({//
                        data: data
                    }));
                }else{
                    let result = HuaYanShiFormat(
                        data,
                        newData,
                        tableName
                    );
                    dispatch(updateData({//
                        data: result[0]
                    }));
                }

                //
                //dispatch(updateStandard(result[1], result[2]));
            }

        });//end requestGetHuaYanShiDataByTableNameAndDate
    }
};//end getData