import React, {Component, Fragment} from 'react';
import {Row, Col, Select} from 'antd';
import moment from 'moment';
import * as actionCreators from "../store/actionCreators";
import {connect} from "react-redux";

const Option = Select.Option;

class TimeShow extends Component {


    render() {
        const day = (['', '', '', '', '', '', '']);
        const {handleTimeChange, person, date, searchFlag} = this.props;
        
        let NowDate = null;
        if(date){
            NowDate = moment();
        }else{
            NowDate = date;
        }

        return (
            <Fragment>
                <Row type="flex" justify="space-around" align="middle">
                    <Col span={4}>{moment(NowDate).format('YYYYMMDD')}</Col>
                    <Col span={4}>{"" + day[moment(NowDate).format('d')]}</Col>
                    <Col span={4}>
                        <Select defaultValue='0' onChange={handleTimeChange}>
                        <Option value='0'>0</Option>
                        <Option value='1'>8</Option>
                        <Option value='2'>16</Option>
                    </Select>
                    </Col>
                    <Col span={4}>{"" + window.localStorage.username}</Col>
                </Row>
            </Fragment>
        );
    }
}

//
const mapStateToProps = (state) => {
    return {
        //date: state.getIn(['burnSysOpRe', 'date']),
        timeChose: state.getIn(['burnSysOpRe', 'timeChose']),
        person: state.getIn(['burnSysOpRe', 'person']),
        date:state.getIn(['searchTable', 'date']),
        searchFlag:state.getIn(['searchTable', 'searchFlag']),
    }
}

const mapDispathToProps = (dispatch) => {
    return {
        handleTimeChange(e) {
            const x = parseInt(e)
            dispatch(actionCreators.changeTimeChose(x))
        }
    }//end return
}


export default connect(mapStateToProps, mapDispathToProps)(TimeShow);
