import React, {Component, Fragment} from 'react';
import {Row, Col, Select} from 'antd';
import moment from 'moment';
import * as actionCreators from "../store/actionCreators";
import {connect} from "react-redux";

const Option = Select.Option;

class TimeShow extends Component {


    render() {
        const day = (['', '', '', '', '', '', '']);
        const {person} = this.props;

        return (
            <Fragment>
                <Row type="flex" justify="space-around" align="middle">
                    <Col span={4}>{moment().format('YYYYMMDD')}</Col>
                    <Col span={4}>{"" + day[moment().format('d')]}</Col>
                    <Col span={4}>{"" + window.localStorage.username}</Col>
                </Row>
            </Fragment>
        );
    }
}

//
const mapStateToProps = (state) => {
    return {
        date: state.getIn(['log', 'date']),
        person: state.getIn(['log', 'person']),
    }
}

const mapDispathToProps = (dispatch) => {
    return {
    }//end return
}


export default connect(mapStateToProps, mapDispathToProps)(TimeShow);
