// true   false
export function roleJudge(roleString){
    //
    let rolesArr = roleString.split(',');

    if(
        rolesArr.includes("1") || 
        rolesArr.includes("2") || 
        rolesArr.includes("3") || 
        rolesArr.includes("4")
    ){
        //
        return false;
    }else{
        //
        return true;
    }
}