//
export function getData(
    date,
    tableName,
    data
) {

    return (dispatch) => {
        //
        requestGetZhongKongShiDataByTableNameAndDate(
            date,
            tableName,
            data
        )
            .then(
                (response) => {

                    if(response['code'] === 0){
                        //
                        let newData = deepCopy(response['data'])

                        let result = ZhongKongShiFormat(
                            data,
                            newData,
                            tableName
                        );

                        dispatch(updateData({//
                            data: result
                        }));
                    }//end if

                }
            )
    }//end return
}//end getData

export function saveData(
    {
        tableType = Table.UPPER_TABLE,//
        date,
        index,
        tableName,
        data,
        num = 1//1
    }
) {

    return (dispatch) => {

        requestSaveZhongKongShiData({
            date: date,
            index: index,
            duty: window.localStorage.duty,
            tableName: tableName,
            id: window.localStorage.id,
            data: data,
            num: num
        })
            .then(
                (response) => {


                console.log("")
                console.log(response)
                console.log("")
                    if (response == Mark.SUCCESS && num == 1) {
                        message.info('');
                    } else if (response == Mark.SUCCESS && num > 1) {
                        message.info('');
                    } else {
                        message.info('');
                    }

                    if (tableType == Table.UPPER_TABLE) {

                        if (num === 1)//
                            updateOperator({
                                data: data,
                                index: index,
                                num: num
                            })
                        else//
                            updateOperator({
                                data: data,
                                num: 27
                            })//27

                    } else {

                    }

                    //
                    dispatch(updateData({data: data}))

                }
            )//end then


    }//end return
}//end saveData


