import * as constants from './constants';
import {message} from "antd";

//
import {
    requestGetZhongKongShiDataByTableNameAndDate,
    requestSaveZhongKongShiData,
} from "../../../../http/request/RequestZhongKongShi"
import {
    Mark,
    Table
} from "../../../../http/constant/Constant"
import {
    updateOperator, ZhongKongShiFormat
} from "../../../../Helper/Format"


import {deepCopy} from "../../../../Helper/Copy";


/**
 * 
 * @param timeChose
 * @returns {{timeChose: *, type: string}}
 */
export const changeTimeChose = (timeChose) => ({
    type: constants.CHANGE_TIME_CHOSE_BSO,
    timeChose: timeChose
});


export const updateData = ({data}) => ({
    type: constants.UPDATE_DATA_BSO,
    data: data
});


/**
 *
 * @param date
 * @param tableName
 * @param data 
 * @returns {Function}
 */
export const getData = (date, tableName, data,flag) => {
    return (dispatch) => {

        requestGetZhongKongShiDataByTableNameAndDate(
            date,
            tableName,
            data
        ).then((response) => {


            if(response['code'] === 0){

                //
                let newData = deepCopy(response['data']);

                let result = ZhongKongShiFormat(
                    data,
                    newData,
                    tableName
                );
                
                if(result != 0){
                    dispatch(updateData({//
                        data: result
                    }));    
                }
            
            }


        });//end requestGetHuaYanShiDataByTableNameAndDate
    }
};//end getData

export function saveData(
    {
        tableType = 1,//
        date,
        index,
        tableName,
        data,
        num = 1//1
    }
) {

    return(dispatch) =>{
        requestSaveZhongKongShiData({
            date: date,
            index: index,
            departmentId: window.localStorage.department,
            tableName: tableName,
            id: window.localStorage.id,
            data: data,
            num: num
        }).then((response) => {

            //
            if (response == Mark.SUCCESS && num == 1) { //eslint-disable-line
                message.info('');
            } else if (response == Mark.SUCCESS && num > 1) {
                message.info('');
            } else {
                message.info('');
            }

            //
            if (tableType === Table.UPPER_TABLE) {//
                if (num === 1)//
                    updateOperator({
                        data: data,
                        index: index,
                        num:num
                    })
                else//
                    updateOperator({
                        data: data,
                        num: 36
                    })//36

                dispatch(updateData({data:data}))//updateData

            }else {
                dispatch(updateData({data:data}))
            }

        });//end requestSaveHuaYanShiData
    }

}


