export function getHuaYanShiDataDifferenceValueBytableNameAndDate(
    date,
    tableName,
    data
) {

    return new Promise((resolve, reject) => {

        //
        const formData = new FormData();
        formData.append('today', date);
        formData.append('yesterday', moment(date).subtract(1,"days").format("YYYY/MM/DD"));
        formData.append('tableName', tableName);

        RequestCenter({
            url: URL.REQUEST_GET_HUAYANSHI_ELEC_DATA,
            formData: formData
        })
            .then((response) => {
                resolve(response)
            })
            .catch()
    });

}


export function requestSaveHuaYanShiData(
    {
        date,
        index,
        departmentId,
        tableName,
        id,
        data,
        num,
    }
) {

    return new Promise((resolve, reject) => {
        RequestCenter({
            url: URL.REQUEST_SAVE_HUAYANSHI_DATA,
            jsonData: getSaveHuaYanShiDataJson(//json
                {
                    date: date,
                    index: index,
                    departmentId: departmentId,
                    tableName: tableName,
                    userId: id,
                    data: data,
                    num: num
                }
            ),
            flag: RequestMethod.jsonDta,
        })
            .then((response) => {
                console.log('response')
                console.log(response)
                console.log('response')
                // 
                //TODO  requestSaveHuaYanShiData
                resolve(response)
            })
            .catch(function(error) {
//  getJSON  
                console.log('', error);
            })
    });

}



