/**
 *  
 * @param tableName
 * @param startValue
 * @param endValue
 * @param reason
 * @param person
 * @param createdAt
 * @returns {Promise<any>}
 */
export function requestSetStandard(
    tableName,
    startValue,
    endValue,
    reason,
    createdAt,
    person
){


    return new Promise((resolve, reject) => {

        RequestCenter({
            url:URL.REQUEST_SET_STANDARD,
            jsonData:getSaveStandardJson(//json
                tableName,
                startValue,
                endValue,
                reason,
                createdAt,
                person
            ),
            flag:RequestMethod.jsonDta,
        })
            .then((response) => {
                // 
                //TODO  requestSetStandard
                resolve(response)
            })
            .catch()
    });

}