import * as constants from './constants';
import {fromJS} from 'immutable';
import moment from 'moment';
import {TableName} from "../../../../Constant/TableNameConstant"

const defaultState = fromJS({

    // 2020/2/27 YYYY/MM/DD 
    date: moment().format("YYYY/MM/DD"),//moment().format("YYYY-MM-DD"),
    requestFlag:true,//
    timeChose: 0,//0
    data:[//
        {data: []}, {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []}, {data: []},

        {data: []}, {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []}, {data: []}

    ],
    modelData:[//
        {data: []}, {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []}, {data: []},

        {data: []}, {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []}, {data: []},
        {data: []}, {data: []}, {data: []}, {data: []}

    ],
    person: window.localStorage.username, //
    tableName:TableName.Analysis_MFA//

});
export default (state = defaultState, action) => {

    switch (action.type) {
        case constants.UPDATE_DATA_MFIAO:
            return state.merge({
                'data': action.data,
                'requestFlag':false//true
            });

        default:
            return state;
    }


}