import {
    Form, Input, Tooltip, Icon, message, Button, 
} from 'antd';
import React from "react";
import {Divider} from "antd";
import "./reg.css";

class Registration extends React.Component {
    state = {
        confirmDirty: false,
        autoCompleteResult: [],
        email: null,
        name: null,//
        password: null,//
        errorMessage: null,
        tel: null,
        messageInfo: [],
    };

    /****/
    handleSubmit = e => {
        e.preventDefault();
        let jsonData = {};
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                jsonData = {
                    'name': values['username'],
                    'phone': values['phone'],
                    'password': values['password'],
                };
            }
        });
        // console.log(jsonData);
        fetch("/api/register", {
            method: 'POST',
            credentials: "include",
            body: JSON.stringify(jsonData),
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(res => res.json())
            .then(data => {
                console.log(data)
                if (data['code'] === 0) {//
                    this.props.history.push("/login");
                }
                else {
                    message.error('');
                }
            })
            .catch(error => console.error('Error:', error))
    };

    /****/
    handleConfirmBlur = e => {
        const value = e.target.value;
        this.setState({confirmDirty: this.state.confirmDirty || !!value});
        if (value !== this.state.password) {
            this.setState({
                errorMessage: ''
            })

        } else {
            this.setState({
                errorMessage: null
            })
        }
    };

    /****/
    compareToFirstPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && value !== form.getFieldValue('password')) {
            callback('');
        } else {
            callback();
        }
    };

    validateToNextPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value.length < 6) {
            callback('')
        }
        if (value && this.state.confirmDirty) {
            form.validateFields(['confirm'], {force: true});
        }
        callback();
    };


    render() {
        const {getFieldDecorator} = this.props.form;

        const formItemLayout = {
            labelCol: {
                xs: {span: 24},
                sm: {span: 6}
            },
            wrapperCol: {
                xs: {span: 24},
                sm: {span: 16}
            }
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0
                },
                sm: {
                    span: 16,
                    offset: 8
                }
            }
        };

        return (
            <div className="register">
                <div className="welcomeRegister">
                    <Divider></Divider>
                </div>
                <Form className="pleaseRegister" {...formItemLayout} onSubmit={this.handleSubmit}>
                    <Form.Item
                        label={(
                            <span>&nbsp;<Tooltip title=""><Icon type="question-circle-o"/></Tooltip></span>)}
                    >
                        {getFieldDecorator('username', {
                            rules: [{required: true, message: '!', whitespace: true}],
                        })(
                            <Input/>
                        )}
                    </Form.Item>
                    <Form.Item
                        label=""
                    >
                        {getFieldDecorator('phone', {
                            rules: [{
                                pattern: '\\d{11}', message: '11',
                            }, {
                                required: true, message: '!',
                            }],
                        })(
                            <Input/>
                        )}
                    </Form.Item>
                    <Form.Item
                        label=""
                    >
                        {getFieldDecorator('password', {
                            rules: [{
                                required: true, message: '',
                            }, {
                                validator: this.validateToNextPassword,
                            }],
                        })(
                            <Input type="password"/>
                        )}
                    </Form.Item>
                    <Form.Item
                        label=""
                    >
                        {getFieldDecorator('confirm', {
                            rules: [{
                                required: true, message: '',
                            }, {
                                validator: this.compareToFirstPassword,
                            }],
                        })(
                            <Input type="password" onBlur={this.handleConfirmBlur}/>
                        )}
                    </Form.Item>
                    <Form.Item {...tailFormItemLayout}>
                        <Button type="primary" htmlType="submit" ></Button>
                        <Button type="primary" htmlType="submit" ></Button>
                    </Form.Item>
                </Form>
            </div>
        );
    }
}


const WrappedRegistrationForm = Form.create({name: 'register'})(Registration);

export default WrappedRegistrationForm;
