import * as constants from './constants';
import {fromJS} from 'immutable';
import moment from 'moment';
// import {TableName} from "../../../Constant/TableNameConstant"

const defaultState = fromJS({

    // 2020/2/27 YYYY/MM/DD 
    date: moment().format("YYYY/MM/DD"),//moment().format("YYYY-MM-DD"),
    requestFlag:true,//
    timeChose: 0,//0
    startTime:"2001/01/01",
    endTime:"2025/01/01",
    data:[//
        {data: []}

    ],
    person: window.localStorage.username, //
    loadFlag:true

});
export default (state = defaultState, action) => {

    switch (action.type) {
        case constants.UPDATE_DATA_TFKT:
            return state.merge({
                'data': action.data,
                'requestFlag':false,//true
                'loadFlag': false
            });
        case constants.UPDATE_START_END_TIME:
            return state.merge({
                'startTime':action.startTime,
                'endTime':action.endTime,
            })

        default:
            return state;
    }


}