import React, { Component,Fragment } from 'react';
import { Row, Col, Select } from 'antd';
import moment from 'moment';
import * as actionCreators from "../store/actionCreators";
import {connect} from "react-redux";

const Option = Select.Option;

class TimeShow extends Component{

    /*constructor(props){
        super(props);
        this.state={
            timeChose:'0'
        }
    }*/

    /**
     *
     */
    /*handleTimeChange = (e) => {
        const x=parseInt(e)
        const timeChose=['0','8','4'];
        this.props.handleTimeChose(x);
        this.setState({
            timeChose:timeChose[x]
        })

    }*/

    render() {
        const day=(['','','','','','','']);
        const {handleTimeChange, person, searchdate, searchFlag} = this.props;

        let NowDate = null;
        if(searchFlag){
            NowDate = moment();
        }else{
            NowDate = searchdate;
        }

        return(
            <Fragment>
                <Row type="flex" justify = "space-around" align="middle">
                    <Col span ={4}>{moment(NowDate).format('YYYYMMDD')}</Col>
                    <Col span ={4}>{""+day[moment(NowDate).format('d')]}</Col>
                    <Col span ={4}>
                    <Select defaultValue='0'  onChange={handleTimeChange}>
                            <Option  value='0'>0</Option>
                            <Option  value='1'>8</Option>
                            <Option  value='2'>4</Option>
                        </Select>
                    </Col>
                    <Col span ={4}>{""+window.localStorage.username}</Col>
                </Row>
            </Fragment>
        );
    }
}

//
const mapStateToProps = (state) => {
    return {
        date: state.getIn(['centralControlDaRe', 'date']),
        timeChose: state.getIn(['centralControlDaRe', 'timeChose']),
        person: state.getIn(['centralControlDaRe', 'person']),
        searchdate:state.getIn(['searchTable', 'date']),
        searchFlag:state.getIn(['searchTable', 'searchFlag']),
    }
}

const mapDispathToProps = (dispatch) => {
    return {
        handleTimeChange(e) {
            const x = parseInt(e)
            dispatch(actionCreators.changeTimeChose(x))
        }
    }//end return
}


export default connect(mapStateToProps, mapDispathToProps)(TimeShow);