import * as constants from './constants';
import {fromJS} from 'immutable';
import moment from 'moment';
import {TableName} from "../../../../Constant/TableNameConstant"
import {deepCopy} from "../../../../Helper/Copy";

const defaultState = fromJS({

    // 2020/2/27 YYYY/MM/DD 
    date: moment().format("YYYY/MM/DD"),//moment().format("YYYY-MM-DD"),
    requestFlag: true,//
    timeChose: 0,//0
    data: [//
        {data: []}, {data: []}, {data: []}, {data: []},


        {data: []}, {data: []}, {data: []}, {data: []},


        {data: []}, {data: []}, {data: []}, {data: []},


    ],
    modelData: [//
        {data: []}, {data: []}, {data: []}, {data: []},


        {data: []}, {data: []}, {data: []}, {data: []},


        {data: []}, {data: []}, {data: []}, {data: []},


    ],
    order: [10,11,12],//
    startValue: [], //
    endValue: [],
    person: window.localStorage.username, //
    width:11,
    tableWidth:13,//
    tableName: TableName.Limestone_CaCO3,//
    allTime : [
        ['','','()',''],
        ['','','()',''],
        ['','','()','']
    ]

});
export default (state = defaultState, action) => {

    switch (action.type) {
        case constants.UPDATE_DATA_RMC:
            return state.merge({
                'data': action.data,
                'requestFlag': false//true
            });
        case constants.CHANGE_TIME_CHOSE_RMC:
            return state.set('timeChose', action.timeChose);

        case constants.UPDATE_STANDARD_RMC://
            return state.merge({
                'startValue': action.startValue,
                'endValue': action.endValue
            });

        default:
            return state;
    }


}