import React, {Component} from 'react';
import './Ibody.css'
import $ from 'jquery';
import {Drawer, Divider, Col, Row, Card} from 'antd';
import {OverPack} from 'rc-scroll-anim';
import QueueAnim from 'rc-queue-anim';
import img1 from "../../img/survey/1.jpg";
import img2 from '../../img/survey/2.jpg';
import img3 from '../../img/survey/3.jpg';
import img4 from '../../img/survey/4.jpg';
import img5 from '../../img/survey/bjsnfh.jpg';
import img6 from '../../img/survey/changhuashiye.jpg';
import img7 from '../../img/survey/djjsnhy.jpg';
import img8 from '../../img/survey/highway01.jpg';

/******/

const pStyle = {
  fontSize: 16,
  color: 'rgba(0,0,0,0.85)',
  lineHeight: '24px',
  display: 'block',
  marginBottom: 16,
  textAlign: 'center',
};

const DescriptionItem = ({title, content}) => {
  return (
    <div
      style={{
        fontSize: 14,
        lineHeight: '22px',
        marginBottom: 7,
        color: 'rgba(0,0,0,0.65)',
      }}
    >
      <p
        style={{
          marginRight: 8,
          display: 'inline-block',
          color: 'rgba(0,0,0,0.85)',
        }}
      >
        {title}:
      </p>
      {content}
    </div>
  );
};

/******/

/******/
const {Meta} = Card;
/******/


export default class Ibody_share extends Component {
  constructor(props) {
    super(props);
    this.state = {
      /**/
      visible: false,
      // 
      data: [
        {   "id": '1',
          "title": '',
          "content": `
                                `,
          "img": img1
        },
        {
          "id" : '2',
          "title": '',
          "content": `
                                
                                `,
          "img": img2
        },
        {
          "id" : '3',
          "title": '',
          "content": `1
                                `,
          "img": img3
        },
        {
          "id" : '4',
          "title": '',
          "content": `123
                                `,
          "img": img4
        },
        {
          "id" : '5',
          "title": '',
          "content": `1
                                `,
          "img": img5
        },
        {
          "id" : '6',
          "title": '',
          "content": `
                                120`,
          "img": img6
        },
        {
          "id" : '7',
          "title": '',
          "content": `
                                `,
          "img": img7
        },
        {
          "id" : '8',
          "title": '',
          "content": ``,
          "img": img8
        },
      ],
      dData: []
    }
  }


  /************/

  showDrawer = (key) => {
    this.setState({
      dData: this.state.data[key-1],
      visible: true,
    });
  };

  onClose = () => {
    this.setState({
      visible: false,
    });
  };

  /************/


  render() {
    const dData = this.state.dData;
    return (
      <div>
        <div id="share" className="share">
          <div className="headtxt">
            <span className="txt1"></span><br/>
          </div>
          <div className="content ">
            {/**/}
            {/*<OverPack playScale={[0.2, 0.5]}>*/}
              {/**/}
              <QueueAnim className="row"
                         key="queue" delay={100} leaveReverse={true}
                         animConfig={{opacity: [1, 0], translateY: [0, 50]}}
              >
                {/*keyabcd...*/}
                <div key="a" style={{width: '100%', height: '100%'}}>
                  {/**/}
                  {
                    this.state.data.map((item) =>(
                      <div className="row-1" key = {item.id}>
                        {/**/}
                        <div
                          className='box'
                          onClick={() => {
                            this.showDrawer(item.id)
                          }}
                        >
                          <div className="icon">
                            <img src ={item.img} alt="" style = {{width:'135px'}} />
                          </div>
                          <div className="context">
                            <p className="news_title">{item.title}</p>
                            <hr/>
                            <p className="news_content">{item.content}</p>
                          </div>
                        </div>
                      </div>
                    ))
                  }
                </div>
              </QueueAnim>
            {/*</OverPack>*/}
          </div>
        </div>
        <Drawer
          width={640}
          placement="right"
          closable={false}
          onClose={this.onClose}
          visible={this.state.visible}
        >
          <Card
            hoverable
            cover={<img alt="example" src={dData.img}/>}
          >
            <Meta
              style={{textAlign: 'center'}}
              title=""
              description=""
            />
          </Card>
          <Divider/>
          <p style={pStyle}></p>
          <Divider/>
          <p>{dData.content}</p>
          <Divider/>
          <p style={pStyle}></p>
          <Divider/>
          <p>&emsp;&emsp;300500</p>
          <Divider/>
          <p style={pStyle}></p>
          <Divider/>
          <Row>
            <Col span={12}>
              <DescriptionItem title="" content=""/>
            </Col>
            <Col span={12}>
              <DescriptionItem title="" content=""/>
            </Col>
          </Row>
          <Row>
            <Col span={12}>
              <DescriptionItem title="" content="100-500"/>
            </Col>
            <Col span={12}>
              <DescriptionItem title="" content="3"/>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <DescriptionItem
                title=""
                content="XXX"
              />
            </Col>
          </Row>
          <Divider/>
          <p style={pStyle}></p>
          <Divider/>
          <Row>
            <Col span={24}>
              <DescriptionItem title="" content="LeeVirtue@SmartLab Design.com"/>
            </Col>
            <Col span={24}>
              <DescriptionItem title="" content="+86 188 0000 0000"/>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <DescriptionItem
                title=""
                content={(
                  <a href="http://www.ccement.com/">
                    www.ccement.com
                  </a>
                )}
              />
            </Col>
          </Row>
        </Drawer>
      </div>


    )
  }
}

